import React from 'react'
import './ContactMe.css'
import { Button } from './Button';
import emailjs from 'emailjs-com';

function ContactMe() {



    function sendEmail(e) {
        e.preventDefault();
        emailjs.sendForm('gmail', 'template_fr3p8mq', e.target, 'user_RFU9T7iieEx5WFh2XxC0i')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
        e.target.reset()
    }

    return (
        <div className="contact">
            <h1>Let's Chat</h1>                             
            <p className='subtitle'></p>

            <div id='contact-container'>
                <div className='contact-info'>
                    <h4>
                        Contact Information
                    </h4>
                    <p></p>
                    <div className='icon-text'>
                        <i className='fa fa-phone' aria-hidden='true'></i>
                        <span>310-343-8777</span>
                    </div>
                    <div className='icon-text'>
                        <i className='fa fa-envelope' aria-hidden='true'></i>
                        <span>ahtan@usc.edu</span>
                    </div>
                    <div className='icon-text'>
                        <i className='fa fa-map-marker' aria-hidden='true'></i>
                        <span>3770 S Figueroa St, Los Angeles, CA 90007</span>
                    </div>
                </div>
                <form onSubmit={sendEmail}>
                    <div className='col'>
                        <div className='form-group'>
                            <label>First Name</label>
                            <input type='text'/>
                        </div>
                        <div className='form-group'>
                            <label>Last Name</label>
                            <input type='text' />
                        </div>
                    </div>

                    <div className='col'>
                        <div className='form-group'>
                            <label>E-Mail</label>
                            <input type='email' />
                        </div>
                        <div className='form-group'>
                            <label>Phone #</label>
                            <input type='tel' />
                        </div>
                    </div>

                    <div className='col'>
                        <div className='form-group solo'>
                            <label>Message</label>
                            <textarea></textarea>
                        </div>
                    </div>

                    <div className='col'>
                        <div className='form-group solo form-button'>
                            <input type='submit' className='btns btn--outline btn--large' value='Send Message'></input>

                        </div>
                    </div>
                </form>
            </div>

        </div>
    )
}

export default ContactMe
